export default {
	data() {
		return {
			id:'',
			password:''
		}
	},
	onLoad() {

	},
	created:function(){
		
	},
	computed: {

	},
	methods: {
		
		login(){
			if(this.id.length <=0 || this.password.length <=0){
				uni.showToast({
					title:'账号或密码不能为空！',
					icon:'none'
				});
				return ;
			}else{
				//console.log(this.websiteUrl+'/user/login');
				uni.request({
					url:this.websiteUrl+'/user/login',
					data:{id:this.id,password:this.password},
					dataType:'json',
					success:res => {
						//console.log(res);
						uni.showToast({
							title:'登陆成功！',
							icon:'none'
						})
						//console.log(res.data);
							uni.request({
							url:this.websiteUrl+'/user/getUser',
							data:{id:this.id},
							dataType:'json',
							
							success: resx => {
								//console.log(resx.data);
								uni.setStorageSync('localuser',resx.data);
							}
							})
						
						
						
						
					},
					fail: res => {
						uni.showToast({
							title:'登陆请求失败！请检查网络',
							icon:'none'
						});
					}
				})
				
			}
			
		}


	}
}
